.directory-item-container {
  max-width: 100%;
  height: 240px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  margin: 0 7.5px 15px;
  overflow: hidden;

  @media (min-width: 768px) {
    width: 30%;
  }

  @media (max-width: 767px) {
    width: 100%;
  }

  &:hover {
    cursor: pointer;

    .background-image {
      transform: scale(1.1);
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }

    .body {
      opacity: 0.9;
    }
  }

  &.large {
    height: 380px;
  }

  &:first-child {
    margin-right: 7.5px;
  }

  &:last-child {
    margin-left: 7.5px;
  }

  .background-image {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
  }

  .body {
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    background-color: white;
    opacity: 0.7;
    position: absolute;

    h2.shopTitle {
      font-weight: normal;
      margin: 0 6px 0;
      font-size: 22px;
      color: #4a4a4a;
      font-family: "Ysabeau Office", sans-serif;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      max-width: 90%;  // テキストがコンテナからはみ出さないように最大幅を設定
    }

    p {
      font-weight: lighter;
      font-size: 16px;
      text-align: center;
    }
  }
}

